<template>
    <div
        v-if="AllowData.ModalInstance.showModal"
        class="modal show d-block"
        tabindex="-1"
        :aria-labelledby="AllowData.ModalInstance.modalId"
        :aria-hidden="!AllowData.ModalInstance.showModal"
        @click="handleBackgroundClick"
        :class="{ 'static-backdrop-animation': AllowData.ModalInstance.animateBackdrop }"
    >
        <div
            class="modal-dialog"
            :class="[AllowData.ModalInstance.size, AllowData.ModalInstance.scrollable ? 'modal-dialog-scrollable' : '']"
        >
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title">{{ AllowData.ModalInstance.title }}</h5>
                    <button type="button" class="btn-close" @click="closeModal"></button>
                </div>
                <div class="modal-body">
                    <div class="btn-group me-2 mb-3" role="group" aria-label="First group">
                        <button
                            type="button"
                            class="btn btn-esun-outline"
                            :class="{ active: AllowData.DisplayMode === 'card' }"
                            @click="AllowData.DisplayMode = 'card'"
                        >
                            卡片顯示
                        </button>
                        <button
                            type="button"
                            class="btn btn-esun-outline"
                            :class="{ active: AllowData.DisplayMode === 'table' }"
                            @click="AllowData.DisplayMode = 'table'"
                        >
                            表格顯示
                        </button>

                        <button type="button" class="btn btn-esun-outline" @click="openAccessBusinessModal">
                            新增授權業務別
                        </button>
                    </div>
                    <div v-if="AllowData.CurrentPageDataList.length === 0" class="text-muted">無項目</div>

                    <div
                        v-if="AllowData.DisplayMode === 'card'"
                        class="d-flex flex-wrap w-100"
                        style="min-height: 400px"
                    >
                        <div
                            v-for="(item, index) in AllowData.CurrentPageDataList"
                            :key="index"
                            class="p-2"
                            style="flex: 1 1 0; min-width: 25%; max-width: 25%; width: 25%"
                        >
                            <div class="card">
                                <div
                                    class="card-body p-2 d-flex flex-column justify-content-between"
                                    style="height: 100px"
                                >
                                    <div class="mb-2 text-truncate" :title="item.allowBusiness" style="width: 100%">
                                        {{ item.allowBusiness }}
                                    </div>
                                    <button
                                        type="button"
                                        class="btn btn-sm btn-danger align-self-end"
                                        @click="RemoveBusiness(item)"
                                    >
                                        移除
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div v-if="AllowData.DisplayMode === 'table'" style="min-height: 400px">
                        <table class="table table-bordered">
                            <thead>
                                <tr>
                                    <th>項目</th>
                                    <th>操作</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="(item, index) in AllowData.CurrentPageDataList" :key="index">
                                    <td>{{ item.allowBusiness }}</td>
                                    <td>
                                        <button
                                            v-if="item.allowBusiness !== business"
                                            type="button"
                                            class="btn btn-sm btn-danger"
                                            @click="RemoveBusiness(item)"
                                        >
                                            移除
                                        </button>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>

                    <Pagination
                        v-if="AllowData.CurrentPageDataList.length > 0"
                        v-model:modelValue="AllowData.CurrentPage"
                        v-model:pageSize="AllowData.PageSize"
                        :total-items="AllowData.TotalItems"
                    />
                </div>
                <div class="modal-footer">
                    <button class="btn btn-esun-outline" @click.prevent="closeModal">關閉</button>
                </div>
            </div>
        </div>
    </div>
    <SelectListModal
        v-if="AccessBusinessModal.ModalInstance.showModal"
        :myModal="AccessBusinessModal.ModalInstance"
        :paginationType="AccessBusinessModal.PaginationType"
        :disabledDataList="AccessBusinessModal.DisabledDataList"
        :selectedList="AccessBusinessModal.DataList"
        :totalItems="AccessBusinessModal.TotalItems"
        :onSelect="handleSelectAllowBusinessSubmit"
        :onCancel="handleSelectAllowBusinessCancel"
        :onPageChange="onPageChange"
    />
</template>
<script setup lang="ts">
import type { BusinessDTO } from '@/models/DTO/BusinessDTO';

const props = defineProps<{
    // 目前選取操作 業務別
    operateBusiness: string;

    // 所有業務別資料
    AllBusinessLists: BusinessDTO[];
}>();
import { ref, onMounted, watch, computed, watchEffect } from 'vue';

import type { MyBaseModal } from '@/components/modal/BaseModal/schema';
import type { AccessibleBusinessList } from '@/api/v1/3_Business/AccessibleBusinessViewData';

// 業務別授權規則

const business = ref<string>(props.operateBusiness);

interface AllowData {
    ModalInstance: MyBaseModal;
    PaginationType: 'server' | 'client';
    DisplayMode: 'table' | 'card';
    FullDataList: AccessibleBusinessList[];
    CurrentPageDataList: AccessibleBusinessList[];
    TotalItems: number;
    CurrentPage: number;
    PageSize: number;
}
const AllowData = ref<AllowData>(GetDefaultAllowData());

function GetDefaultAllowData(): AllowData {
    return {
        ModalInstance: {
            modalId: 'AllowData',
            title: business.value + ' 授權業務別管理 ',
            showModal: false,
            size: 'modal-xl',
            staticBackdrop: true,
            animateBackdrop: false
        },
        PaginationType: 'client',
        DisplayMode: 'table',
        FullDataList: [],
        CurrentPageDataList: [],
        TotalItems: 2,
        CurrentPage: 1,
        PageSize: 10
    };
}

watchEffect(() => {
    // 監聽 operateBusiness 屬性變化
    business.value = props.operateBusiness;
    // 更新 AllowData 的標題
    AllowData.value.ModalInstance.title = business.value + ' 授權業務別管理 ';
});

watch(
    () => ({
        currentPage: AllowData.value.CurrentPage,
        pageSize: AllowData.value.PageSize
    }),
    () => {
        // Client-side pagination logic will go here if needed
        LoadViewData();
    }
);

// 監聽 business 以更新標題和重新載入數據
watch(
    () => business.value,
    (newVal, oldVal) => {
        if (newVal !== oldVal) {
            AllowData.value = GetDefaultAllowData();
            LoadViewData();
        }
    }
);

/**
 * @todo 展開Modal
 */
async function openModal() {
    AllowData.value = GetDefaultAllowData();
    await LoadViewData();
    AllowData.value.ModalInstance.showModal = true;
}

/**
 * @todo 關閉Modal
 */
function closeModal() {
    AllowData.value.ModalInstance.showModal = false;
}

/**
 * @todo 處理點擊背景事件，觸發背景動畫
 * @param event
 */
function handleBackgroundClick(event: MouseEvent) {
    if (event.target === event.currentTarget) {
        AllowData.value.ModalInstance.animateBackdrop = true;
        setTimeout(() => {
            AllowData.value.ModalInstance.animateBackdrop = false;
        }, 150);
    }
}

/**
 * @todo 載入資料
 */
const LoadViewData = async () => {
    try {
        const response = await API.AccessibleBusinessViewData(business.value);

        if (response.status) {
            AllowData.value.FullDataList = response.result.items;
        } else {
            ApiClient.HandleGeneralRespError(response);
        }
        // 分頁處理
        if (AllowData.value.FullDataList.length > 0) {
            const startIndex = (AllowData.value.CurrentPage - 1) * AllowData.value.PageSize;
            const endIndex = AllowData.value.CurrentPage * AllowData.value.PageSize;

            AllowData.value.CurrentPageDataList = AllowData.value.FullDataList.slice(startIndex, endIndex);
            AllowData.value.TotalItems = AllowData.value.FullDataList.length;
        }
    } catch (error) {
        console.error('Error fetching business:', error);
        // Handle error (e.g., show an error message)
    } finally {
    }
};

import {
    SimplePopUp,
    SimplePopUpBtnType,
    SimplePopUpModalType,
    SimplePopUpSize,
    type SimplePopupResolveResult
} from '@/libs/SimplePopUp';
import { GetUpdateBusinessModal } from './UpdateBusinessModal';

const simplePopUp = new SimplePopUp();

/**
 * @todo 移除授權業務別
 * @param item
 */
async function RemoveBusiness(item: AccessibleBusinessList) {
    // 刪除邏輯（可加 confirm）
    let action: SimplePopupResolveResult<string> = await simplePopUp.showAlert(
        SimplePopUpModalType.Warning,
        '警告',
        `確定要刪除授權業務別 ${item.allowBusiness} 嗎？`,
        [
            {
                id: 'cancel',
                type: SimplePopUpBtnType.callback,
                btnClass: 'btn-esun-outline ',
                resolveParam: 'cancel',
                title: '取消'
            },
            {
                id: 'confirm',
                type: SimplePopUpBtnType.callback,
                btnClass: 'btn-info',
                resolveParam: 'confirm',
                title: '確定'
            }
        ]
    );

    if (action.param === 'confirm') {
        const response = await API.AccessibleBusinessRemove(business.value, item.allowBusiness);
        if (response.status) {
            await LoadViewData();
        } else {
            ApiClient.HandleGeneralRespError(response);
        }
    }
}
// ---------------------------------------------------------------------------//

/** 選擇列表模態框 */
// 新增選擇列表模態框配置
import SelectListModal from '@/components/modal/SelectListModal/SelectListModal.vue';
import type { InternalSelectedList, MySelectListModal } from '@/components/modal/SelectListModal/schema';
import * as ApiClient from '@/libs/ApiClient';
import API from '@/api/v1/';
import Pagination from '@/components/pagination/Pagination.vue';

interface ISelectListModal {
    ModalInstance: MySelectListModal;
    PaginationType: 'server' | 'client';
    // 禁用的項目列表
    DisabledDataList: string[] | null;
    // 選擇列表數據
    DataList: InternalSelectedList[];
    TotalItems: number;
    PageNumber: number;
    PageSize: number;
}
const AccessBusinessModal = ref<ISelectListModal>(GetDefaultAccessBusinessModal());

function GetDefaultAccessBusinessModal(): ISelectListModal {
    return {
        ModalInstance: {
            modalId: 'business-select-list-modal',
            title: '選擇業務別',
            showModal: false,
            size: 'modal-xl',
            staticBackdrop: true,
            scrollable: true
        },
        PaginationType: 'server',
        DisabledDataList: AllowData.value.FullDataList.map((item) => item.business),
        DataList: [],
        TotalItems: 0,
        PageNumber: 1,
        PageSize: 10
    };
}

function initAccessBusinessModal() {
    // 初始化選擇列表為全部業務資料
    // 排除目前已存在business
    AccessBusinessModal.value = GetDefaultAccessBusinessModal();
}

const onPageChange = async (searchQuery: string, pageNumber: number, pageSize: number) => {
    try {
        // 目前 business 資料少 分頁在前端做
        const response = await API.BusinessManagementViewData(searchQuery, pageNumber, pageSize);

        if (response.status && response.result?.items) {
            AccessBusinessModal.value.DataList = response.result.items.map((item) => ({
                name: item.business,
                value: item.business
            }));
            AccessBusinessModal.value.TotalItems = response.result.totalCount;
            AccessBusinessModal.value.PageSize = pageSize;
        } else {
            ApiClient.HandleGeneralRespError(response);
        }
    } catch (error) {
        console.error('Error fetching business:', error);
        // Handle error (e.g., show an error message)
    }
};

// 開啟選擇列表模態框
async function openAccessBusinessModal() {
    // 初始化選擇列表為全部業務資料
    initAccessBusinessModal();
    // 排除目前已存在business
    const resp = await API.BusinessManagementViewData('', 1, AccessBusinessModal.value.PageSize);
    // const allowedBusinessSet = new Set(AllowData.value.FullDataList.map((item) => item.business));

    // const filteredList = props.AllBusinessLists.filter((item) => !allowedBusinessSet.has(item.business));
    AccessBusinessModal.value.DataList = resp.result.items.map((item) => ({
        name: item.business,
        value: item.business
    }));
    AccessBusinessModal.value.TotalItems = resp.result.totalCount;
    AccessBusinessModal.value.PageSize = 1;
    AccessBusinessModal.value.ModalInstance.showModal = true;
}

// 處理選擇列表模態框提交
async function handleSelectAllowBusinessSubmit(addedBusiness: string) {
    // 這裡可以根據需求處理選擇結果，例如更新業務資料或其他操作
    // 刪除邏輯（可加 confirm）
    let action: SimplePopupResolveResult<string> = await simplePopUp.showAlert(
        SimplePopUpModalType.Warning,
        '提醒',
        `確定要將 ${addedBusiness} 加入到 ${business.value}授權業務別中嗎？`,
        [
            {
                id: 'cancel',
                type: SimplePopUpBtnType.callback,
                btnClass: 'btn-esun-outline ',
                resolveParam: 'cancel',
                title: '取消'
            },
            {
                id: 'confirm',
                type: SimplePopUpBtnType.callback,
                btnClass: 'btn-info',
                resolveParam: 'confirm',
                title: '確定'
            }
        ]
    );

    if (action.param === 'confirm') {
        const response = await API.AccessibleBusinessAdd(business.value, addedBusiness);
        if (response.status) {
            await LoadViewData();
            AccessBusinessModal.value.ModalInstance.showModal = false;
        } else {
            ApiClient.HandleGeneralRespError(response);
        }
    }
    console.log('選擇列表模態框提交，選擇項目:', addedBusiness);
}

// 處理選擇列表模態框取消
function handleSelectAllowBusinessCancel() {
    AccessBusinessModal.value.ModalInstance.showModal = false;
}

// ---------------------------------------------------------------------------//
// 將方法暴露給父元件使用
defineExpose({ openModal });
</script>

<style scoped>
.modal {
    background-color: rgba(0, 0, 0, 0.5);
}
.modal-dialog {
    background-color: transparent;
}

.static-backdrop-animation {
    animation: staticBackdropAnimation 0.15s ease-in-out;
}

.modal-dialog-scrollable .modal-body {
    max-height: calc(100vh - 200px);
    overflow-y: auto;
}

@keyframes staticBackdropAnimation {
    0% {
        transform: scale(1);
    }
    50% {
        transform: scale(1.02);
    }
    100% {
        transform: scale(1);
    }
}
</style>
