<style scoped>
.content {
    margin-left: 250px;
    transition: margin-left 0.3s;
}
.content.expanded {
    margin-left: 0;
}

.fade-in {
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
}

.fade-in.active {
    opacity: 1;
}
</style>

<template>
    <div class="p-4 mt-5">
        <!-- top button  -->
        <div
            class="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center pt-3 pb-2 mb-3 border-bottom"
        >
            <h1 class="h2">{{ RouteUrlList.BusinessManagement.label }}</h1>
            <div class="btn-toolbar justify-content-end mb-3" role="toolbar" aria-label="Toolbar with button groups">
                <div class="btn-group me-2" role="group" aria-label="First group">
                    <!-- 新增搜尋輸入框 -->
                    <div class="input-group ms-3" style="max-width: 200px">
                        <input
                            type="text"
                            class="form-control form-control-esun"
                            placeholder="搜尋系統"
                            v-model="appData.searchQuery"
                        />
                        <div
                            class="btn-close position-absolute end-0 top-50 translate-middle cursor-pointer shadow-none"
                            @click="clearSearch"
                            aria-label="清除搜尋"
                        >
                            <button class="btn btn-link p-0" aria-label="Close"></button>
                        </div>
                    </div>
                    <button class="btn btn-sm btn-esun-outline ml-3" @click="throttledHandleSearch">搜尋</button>
                    <button class="btn btn-esun-primary ml-3" @click="open">新增業務別</button>
                </div>
                <JsonFormModal
                    v-if="createModal.showModal"
                    :myModal="createModal"
                    :modelValue="createData"
                    @submit="handleCreateSubmit"
                    @cancel="createModal.showModal = false"
                />

                <!-- 切換顯示模式 -->
                <div class="btn-group" role="group">
                    <button
                        class="btn btn-esun-outline"
                        :class="{ active: appData.displayMode === 'card' }"
                        @click="appData.displayMode = 'card'"
                    >
                        卡片模式
                    </button>
                    <button
                        class="btn btn-esun-outline"
                        :class="{ active: appData.displayMode === 'table' }"
                        @click="appData.displayMode = 'table'"
                    >
                        表格模式
                    </button>
                </div>
            </div>
        </div>

        <div v-if="appData.totalCount === 0">無 資 料 !</div>
        <!-- 顯示資料 -->
        <Pagination
            v-if="appData.totalCount"
            v-model:modelValue="appData.pageNumber"
            v-model:pageSize="appData.pageSize"
            :total-items="appData.totalCount"
        />
        <div
            v-if="appData.displayMode === 'card'"
            class="row row-cols-1 row-cols-md-2 row-cols-lg-3 g-3 active"
            :class="{ 'fade-in': appData.pageNumber }"
            ref="dataContainer"
        >
            <div class="col" v-for="item in appData.business" :key="item.business">
                <div class="card card-esun h-100 esun-animate-in">
                    <div class="card-body">
                        <h5 class="card-title">{{ item.business }}</h5>
                        <p class="card-text">
                            <strong>系統代號:</strong> {{ item.system }}<br />
                            <strong>是否軟刪除:</strong>
                            <span :class="item.isDeleted === 'N' ? 'text-success' : 'text-danger'">
                                {{ item.isDeleted === 'Y' ? '是' : '否' }} </span
                            ><br />

                            <strong>啟用狀態:</strong>
                            <span :class="item.isActive === 'Y' ? 'text-success' : 'text-danger'">
                                {{ item.isActive === 'Y' ? '啟用中' : '已停用' }} </span
                            ><br />

                            <strong>token:</strong> {{ item.token }}<br />

                            <strong>建立時間:</strong> {{ Common.FormatDate(item.createdTime) }}<br />
                            <strong>更新時間:</strong> {{ Common.FormatDate(item.lastUpdate) }}
                        </p>

                        <!-- 按鈕群組 -->
                        <!-- Dropdown 按鈕 -->
                        <div class="dropdown mt-3">
                            <button
                                class="btn btn-esun-outline dropdown-toggle w-100"
                                type="button"
                                data-bs-toggle="dropdown"
                                aria-expanded="false"
                            >
                                <i class="bi bi-gear me-1"></i> 操作選單
                            </button>
                            <ul class="dropdown-menu w-100">
                                <li>
                                    <a class="dropdown-item" href="#" @click.prevent="handleEdit(item)">
                                        <i class="bi bi-pencil-square me-1 text-primary"></i> 修改
                                    </a>
                                </li>
                                <li>
                                    <a class="dropdown-item" href="#" @click.prevent="handleDelete(item)">
                                        <i class="bi bi-trash me-1 text-danger"></i> 刪除
                                    </a>
                                </li>
                                <li>
                                    <a
                                        class="dropdown-item"
                                        href="#"
                                        @click.prevent="handleDeactivate(item)"
                                        :class="{ disabled: !item.isActive }"
                                    >
                                        <i class="bi bi-slash-circle me-1 text-warning"></i> 停用
                                    </a>
                                </li>
                                <li>
                                    <a
                                        class="dropdown-item"
                                        href="#"
                                        @click.prevent="showBusinessAllowModal(item.business)"
                                    >
                                        <i class="bi bi-trash me-1 text-info"></i> 授權業務別管理
                                    </a>
                                </li>
                            </ul>
                        </div>
                        <!-- Dropdown 按鈕 -->
                    </div>
                </div>
            </div>
        </div>

        <div v-else :class="{ 'fade-in': appData.pageNumber }" style="overflow: auto" class="active">
            <table class="table table-bordered table-hover align-middle">
                <thead class="table-light">
                    <tr>
                        <th>業務別</th>
                        <th>系統代號</th>
                        <th>是否軟刪除</th>
                        <th>啟用狀態</th>
                        <th>token</th>
                        <th>建立時間</th>
                        <th>更新時間</th>
                        <th>操作選單</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="item in appData.business" :key="item.business">
                        <td>{{ item.business }}</td>
                        <td>{{ item.system }}</td>
                        <td>
                            <span :class="item.isDeleted ? 'text-success' : 'text-danger'">
                                {{ item.isDeleted ? '啟用中' : '已停用' }}
                            </span>
                        </td>

                        <td>
                            <span :class="item.isActive ? 'text-success' : 'text-danger'">
                                {{ item.isActive ? '啟用中' : '已停用' }}
                            </span>
                        </td>

                        <td>{{ item.token }}</td>
                        <td>{{ Common.FormatDate(item.createdTime) }}</td>
                        <td>{{ Common.FormatDate(item.lastUpdate) }}</td>
                        <td>
                            <!-- 按鈕群組 -->
                            <!-- Dropdown 按鈕 -->
                            <div class="dropdown mt-3">
                                <button
                                    class="btn btn-esun-outline dropdown-toggle w-100"
                                    type="button"
                                    data-bs-toggle="dropdown"
                                    aria-expanded="false"
                                >
                                    <i class="bi bi-gear me-1"></i> 操作選單
                                </button>
                                <ul class="dropdown-menu w-100">
                                    <li>
                                        <a class="dropdown-item" href="#" @click.prevent="handleEdit(item)">
                                            <i class="bi bi-pencil-square me-1 text-primary"></i> 修改
                                        </a>
                                    </li>
                                    <li>
                                        <a class="dropdown-item" href="#" @click.prevent="handleDelete(item)">
                                            <i class="bi bi-trash me-1 text-danger"></i> 刪除
                                        </a>
                                    </li>
                                    <li>
                                        <a
                                            class="dropdown-item"
                                            href="#"
                                            @click.prevent="handleDeactivate(item)"
                                            :class="{ disabled: !item.isActive }"
                                        >
                                            <i class="bi bi-slash-circle me-1 text-warning"></i> 停用
                                        </a>
                                    </li>
                                    <li>
                                        <a
                                            class="dropdown-item"
                                            href="#"
                                            @click.prevent="showBusinessAllowModal(item.business)"
                                        >
                                            <i class="bi bi-trash me-1 text-info"></i> 授權業務別管理
                                        </a>
                                    </li>
                                </ul>
                            </div>
                            <!-- Dropdown 按鈕 -->
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>

        <Pagination
            v-if="appData.totalCount"
            v-model:modelValue="appData.pageNumber"
            v-model:pageSize="appData.pageSize"
            :total-items="appData.totalCount"
        />

        <JsonFormModal
            v-if="updateModal.showModal"
            :myModal="updateModal"
            v-model="updateData"
            @submit="handleEditSubmit"
            @cancel="updateModal.showModal = false"
        />
    </div>
    <BusinessAllowModal
        v-if="operateBusiness"
        ref="BusinessAllowModalRef"
        :operateBusiness="operateBusiness"
        :AllBusinessLists="appData.business"
    />
</template>

<script setup lang="ts">
import { ref, onMounted, watch, computed, reactive, nextTick } from 'vue';
defineProps<{ collapsed: boolean; paginationType?: 'client' | 'server' }>();
defineEmits(['toggleTheme']);

import Pagination from '@/components/pagination/Pagination.vue';
import * as Common from '@/libs/Common';
import type { BusinessDTO } from '@/models/DTO/BusinessDTO';
import * as ApiClient from '@/libs/ApiClient';
import API from '@/api/v1/';
import JsonFormModal from '@/components/modal/JsonFormModal/JsonFormModal.vue';

import { GetCreateBusinessModal, GetDefaultFormData } from './CreateBusinessModal';
import type { MyModal } from '@/components/modal/JsonFormModal/schema';
import { RouteUrlList } from '@/libs/RouteList';
import type { SystemDTO } from '@/models/DTO/SystemDTO';

// business 資料（ API 傳入）
// 取得所有系統資訊
const systemList = ref<SystemDTO[]>([]);
const GetAllSystemInfo = async () => {
    const resp = await API.GetAllSystemInfo();
    if (resp.status === false) {
        ApiClient.HandleGeneralRespError(resp.message);
        return;
    }
    systemList.value = resp.result.items;
};

interface IAppData {
    business: BusinessDTO[];
    searchQuery: string;
    paginationType: 'server' | 'client';
    pageNumber: number; // 可以根據 pageNumber 與 pageSize 去查詢後端資料
    pageSize: number;
    totalCount: number;
    displayMode: 'card' | 'table';
}
const appData = reactive<IAppData>({
    business: [],
    searchQuery: '',
    paginationType: 'server',
    pageNumber: 1,
    pageSize: 10,
    totalCount: 0,
    displayMode: 'card'
});

// 建立business 相關
const createModal = ref<MyModal>(GetCreateBusinessModal(systemList.value));
const createData = ref<BusinessDTO>(GetDefaultFormData());
/**
 * @todo 載入資料
 */
const LoadViewData = async () => {
    try {
        // 目前 business 資料少 分頁在前端做
        const response = await API.BusinessManagementViewData(
            appData.searchQuery,
            appData.pageNumber,
            appData.pageSize
        );

        if (response.status && response.result?.items) {
            appData.business = response.result.items;

            appData.totalCount = response.result.totalCount;
        } else {
            ApiClient.HandleGeneralRespError(response);
        }
    } catch (error) {
        ApiClient.HandleError(error);
        console.error('Error fetching business:', error);
        // Handle error (e.g., show an error message)
    } finally {
    }
};

onMounted(async () => {
    await GetAllSystemInfo();
    createModal.value = GetCreateBusinessModal(systemList.value);
    updateModal.value = GetUpdateBusinessModal(systemList.value);
    // 初始化資料
    await LoadViewData();
});

watch(
    () => ({
        pageNumber: appData.pageNumber,
        pageSize: appData.pageSize
    }),
    () => {
        LoadViewData();
    }
);

function open() {
    createModal.value.showModal = true;
}

async function handleCreateSubmit(formData: Record<string, any>) {
    try {
        const item: BusinessDTO = formData as BusinessDTO;

        // 刪除邏輯（可加 confirm）
        let action: SimplePopupResolveResult<string> = await simplePopUp.showAlert(
            SimplePopUpModalType.Warning,
            '警告',
            `確定新增系統 ${item.system} 嗎？`,
            [
                {
                    id: 'cancel',
                    type: SimplePopUpBtnType.callback,
                    btnClass: 'btn-esun-outline ',
                    resolveParam: 'cancel',
                    title: '取消'
                },
                {
                    id: 'confirm',
                    type: SimplePopUpBtnType.callback,
                    btnClass: 'btn-info',
                    resolveParam: 'confirm',
                    title: '確定'
                }
            ]
        );

        if (action.param === 'confirm') {
            let postData: BusinessManagementCreateParams = {
                business: item.business,
                systemName: item.system,
                isDeleted: item.isDeleted === 'Y' ? true : false,
                isActive: item.isActive === 'Y' ? true : false,
                token: item.token
            };
            const response = await API.BusinessManagementCreate(postData);
            if (response.status) {
                await LoadViewData(); // 重新載入資料
            } else {
                ApiClient.HandleGeneralRespError(response);
            }
        }

        createModal.value.showModal = false;
    } catch (error) {
        ApiClient.HandleError(error);
        console.error('Error fetching business:', error);
        // Handle error (e.g., show an error message)
    }
}

// 搜尋系統資料
const handleSearch = () => {
    LoadViewData();
};
// 使用節流函式包裝input事件處理器，提高效能
const throttledHandleSearch = Common.Throttle(handleSearch, 100);

const clearSearch = () => {
    appData.searchQuery = '';
    LoadViewData();
};

// 卡片 事件處理函式
// 建立system 相關
import {
    SimplePopUp,
    SimplePopUpBtnType,
    SimplePopUpModalType,
    SimplePopUpSize,
    type SimplePopupResolveResult
} from '@/libs/SimplePopUp';
import { GetUpdateBusinessModal } from './UpdateBusinessModal';

const simplePopUp = new SimplePopUp();

const updateModal = ref<MyModal>(GetUpdateBusinessModal(systemList.value));
const updateData = ref<BusinessDTO>(GetDefaultFormData());

function handleEdit(item: BusinessDTO) {
    // 編輯邏輯
    updateData.value = { ...item }; // 深拷貝，避免直接修改原始資料
    updateModal.value.showModal = true;
}

async function handleEditSubmit(formData: Record<string, any>) {
    try {
        // 更新邏輯
        updateModal.value.showModal = false;

        const item: BusinessDTO = formData as BusinessDTO;
        // 刪除邏輯（可加 confirm）
        let action: SimplePopupResolveResult<string> = await simplePopUp.showAlert(
            SimplePopUpModalType.Warning,
            '警告',
            `確定要更新系統 ${item.system} 嗎？`,
            [
                {
                    id: 'cancel',
                    type: SimplePopUpBtnType.callback,
                    btnClass: 'btn-esun-outline ',
                    resolveParam: 'cancel',
                    title: '取消'
                },
                {
                    id: 'confirm',
                    type: SimplePopUpBtnType.callback,
                    btnClass: 'btn-info',
                    resolveParam: 'confirm',
                    title: '確定'
                }
            ]
        );

        if (action.param === 'confirm') {
            let postData: BusinessManagementUpdateParams = {
                business: item.business,
                systemName: item.system,
                isDeleted: item.isDeleted === 'Y' ? true : false,
                isActive: item.isActive === 'Y' ? true : false,
                token: item.token
            };
            const response = await API.BusinessManagementUpdate(postData);
            if (response.status) {
                await LoadViewData(); // 重新載入資料
            } else {
                ApiClient.HandleGeneralRespError(response);
            }
        }
    } catch (error) {
        ApiClient.HandleError(error);
        console.error('Error fetching business:', error);
        // Handle error (e.g., show an error message)
    }
}

/**
 * @todo 刪除系統
 * @param item
 */
async function handleDelete(item: BusinessDTO) {
    try {
        // 刪除邏輯（可加 confirm）
        let action: SimplePopupResolveResult<string> = await simplePopUp.showAlert(
            SimplePopUpModalType.Warning,
            '警告',
            `確定要刪除系統 ${item.system} 嗎？`,
            [
                {
                    id: 'cancel',
                    type: SimplePopUpBtnType.callback,
                    btnClass: 'btn-esun-outline ',
                    resolveParam: 'cancel',
                    title: '取消'
                },
                {
                    id: 'confirm',
                    type: SimplePopUpBtnType.callback,
                    btnClass: 'btn-info',
                    resolveParam: 'confirm',
                    title: '確定'
                }
            ]
        );

        if (action.param === 'confirm') {
            const response = await API.BusinessManagementDelete(item.business);
            if (response.status) {
                await LoadViewData(); // 重新載入資料
            } else {
                ApiClient.HandleGeneralRespError(response);
            }
        }
    } catch (error) {
        ApiClient.HandleError(error);
        console.error('Error fetching business:', error);
        // Handle error (e.g., show an error message)
    }
}

/**
 * @todo 停用系統
 */
async function handleDeactivate(item: BusinessDTO) {
    try {
        // 停用邏輯
        const action: SimplePopupResolveResult<string> = await simplePopUp.showAlert(
            SimplePopUpModalType.Warning,
            '警告',
            '確定要停用此系統嗎？',
            [
                {
                    id: 'cancel',
                    type: SimplePopUpBtnType.callback,
                    btnClass: 'btn-esun-outline',
                    resolveParam: 'cancel',
                    title: '取消'
                },
                {
                    id: 'confirm',
                    type: SimplePopUpBtnType.callback,
                    btnClass: 'btn-warning',
                    resolveParam: 'confirm',
                    title: '確定'
                }
            ]
        );
        if (action.param === 'confirm') {
            const response = await API.BusinessManagementDeactivate(item.business);
            if (response.status) {
                await LoadViewData(); // 重新載入資料
            } else {
                ApiClient.HandleGeneralRespError(response);
            }
        }
    } catch (error) {
        ApiClient.HandleError(error);
        console.error('Error fetching business:', error);
        // Handle error (e.g., show an error message)
    }
}

/**
 * @todo 業務別授權管理
 */
const operateBusiness = ref<string>('');
import BusinessAllowModal from '@/components/BackStage/3_Business/BusinessAllowModal.vue';
import type { BusinessManagementUpdateParams } from '@/api/v1/3_Business/BusinessManagementUpdate';
import type { BusinessManagementCreateParams } from '@/api/v1/3_Business/BusinessManagementCreate';

const BusinessAllowModalRef = ref<InstanceType<typeof BusinessAllowModal>>();

/**
 * @todo 開啟 業務別授權
 */
const showBusinessAllowModal = (business: string) => {
    operateBusiness.value = business;
    nextTick(() => {
        if (BusinessAllowModalRef.value) {
            BusinessAllowModalRef.value?.openModal();
        }
    });
};
</script>
