import { ref, onMounted, watch, computed } from 'vue';
import type { MyModal } from '@/components/modal/JsonFormModal/schema';
import type { BusinessDTO } from '@/models/DTO/BusinessDTO';
import * as Common from '@/libs/Common';
import type { SystemDTO } from '@/models/DTO/SystemDTO';

// 定義測試欄位群組與欄位
export const GetCreateBusinessModal = (systemList: SystemDTO[]): MyModal => {
    return {
        modalId: 'myModal',
        title: '新增系統業務別',
        showModal: false,
        size: 'modal-xl',
        staticBackdrop: true,
        scrollable: true,
        fieldGroups: [
            {
                title: '',
                gridClass: 'col-12  ',
                fields: [
                    {
                        modelId: 'business',
                        name: 'business',
                        type: 'text',
                        label: '業務別名稱',
                        placeholder: '業務別名稱',
                        required: true,
                        maxlength: 25,
                        onInputCallback: (value: string) => {
                            return Common.ReplaceSpecialChar(value);
                        }
                    },
                    {
                        modelId: 'system',
                        name: 'system',
                        type: 'select',
                        label: '系統代號',
                        hint: '關聯的系統代號',
                        required: true,
                        options: systemList?.map((system) => ({
                            label: system.system,
                            value: system.system
                        }))
                    },
                    {
                        modelId: 'isActive',
                        name: 'isActive',
                        type: 'radio',
                        label: '是否啟用',
                        inline: true,
                        options: [
                            { label: '啟用', value: 'Y' },
                            { label: '停用', value: 'N' }
                        ],
                        required: true
                    },
                    {
                        modelId: 'isDeleted',
                        name: 'isDeleted',
                        type: 'radio',
                        label: '是否刪除',
                        inline: true,
                        options: [
                            { label: '刪除', value: 'Y' },
                            { label: '不刪除', value: 'N' }
                        ],
                        required: true
                    },
                    {
                        modelId: 'token',
                        name: 'token',
                        type: 'text',
                        label: '業務別的密碼(明碼)',
                        hint: '若留空則系統自動產生32位隨機字串',
                        maxlength: 32,
                        required: false
                    }
                ]
            }
        ]
    };
};

export interface BusinessFormData extends BusinessDTO {}
export const GetDefaultFormData = (): BusinessFormData => {
    return {
        business: '',
        system: '',

        isActive: 'Y',
        isDeleted: 'N',
        token: '',
        base64TokenSignature: '',
        base64Salt: '',
        pepper: '',
        createdTime: '',
        lastUpdate: null
    };
};
